            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.link">MP3 256</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="getMp3(downloadData.link, '192')">MP3 192</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.hq_link">M4A High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.lq_link">M4A Low</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.hq_hls">M3U8 High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadMp3')" :href="downloadData.lq_hls">M3U8 Low</a>
        </b-modal>
        <b-modal class="downloadModal" id="downloadPodcast" centered hide-footer title="Select Quality">
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="getPodcast(downloadData.link, '256')">MP3 256</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.link">MP3 192</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.hq_link">M4A High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.lq_link">M4A Low</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.hq_hls">M3U8 High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadPodcast')" :href="downloadData.lq_hls">M3U8 Low</a>
        </b-modal>
        <b-modal class="downloadModal" id="downloadVideo" centered hide-footer title="Select Quality">
            <a class="btn btn-primary btn-block" @click="hideModal('downloadVideo')" :href="downloadData.high_web">MP4 High</a>
            <a class="btn btn-primary btn-block" @click="hideModal('downloadVideo')" :href="downloadData.low_web">MP4 Low</a>
        </b-modal>
        <b-modal class="downloadModal" id="downloadCollection" scrollable centered hide-footer size="lg" @hidden="clearCollection">
            <b-tabs pills id="podcast">
                <b-tab title="MP3">
                    <Loading v-if="collectionLink.length === 0"></Loading>
                    <div class="mt-3" v-if="collectionLink.length > 0">
                        <button class="btn btn-info btn-block mb-2" v-clipboard:copy="collectionLink.join('\r\n')">Copy To Clipboard</button>
                        <div v-for="(link,index) in collectionLink" :key="index" v-text="link"></div>
                    </div>
                </b-tab>
                <b-tab title="Cover">
                    <Loading v-if="collectionPhoto.length === 0"></Loading>
                    <div class="mt-3" v-if="collectionPhoto.length > 0">
                        <button class="btn btn-info btn-block mb-2" v-clipboard:copy="collectionPhoto.join('\r\n')">Copy To Clipboard</button>
                        <div v-for="(photo,index) in collectionPhoto" :key="index" v-text="photo"></div>
                    </div>
                </b-tab>
            </b-tabs>
        </b-modal>
        <b-modal class="downloadModal" id="downloadAlbum" scrollable centered hide-footer size="lg">
            <b-tabs pills id="album">
                <b-tab title="MP3">
                    <div class="mt-3" v-if="albumLink.mp3.length > 0">
                        <button class="btn btn-info btn-block mb-2" v-clipboard:copy="albumLink.mp3.join('\r\n')">Copy To Clipboard</button>
                        <div v-for="(link,index) in albumLink.mp3" :key="index" v-text="link"></div>
                    </div>
                </b-tab>
                <b-tab title="M4A">
                    <div class="mt-3" v-if="albumLink.m4a.length > 0">
                        <button class="btn btn-info btn-block mb-2" v-clipboard:copy="albumLink.m4a.join('\r\n')">Copy To Clipboard</button>
                        <div v-for="(link,index) in albumLink.m4a" :key="index" v-text="link"></div>
                    </div>
                </b-tab>
                <b-tab title="M3U8">
                    <div class="mt-3" v-if="albumLink.m3u8.length > 0">
                        <button class="btn btn-info btn-block mb-2" v-clipboard:copy="albumLink.m3u8.join('\r\n')">Copy To Clipboard</button>
                        <div v-for="(link,index) in albumLink.m3u8" :key="index" v-text="link"></div>
                    </div>
                </b-tab>
            </b-tabs>
        </b-modal>
        <b-modal class="downloadModal" id="downloadPlaylist" scrollable centered hide-footer size="lg">
            <b-tabs pills id="playlist">
                <b-tab title="MP3">
                    <div class="mt-3" v-if="playlistLink.mp3.length > 0">
                        <button class="btn btn-info btn-block mb-2" v-clipboard:copy="playlistLink.mp3.join('\r\n')">Copy To Clipboard</button>
                        <div v-for="(link,index) in playlistLink.mp3" :key="index" v-text="link"></div>
                    </div>
                </b-tab>
                <b-tab title="M4A">
                    <div class="mt-3" v-if="playlistLink.m4a.length > 0">
                        <button class="btn btn-info btn-block mb-2" v-clipboard:copy="playlistLink.m4a.join('\r\n')">Copy To Clipboard</button>
                        <div v-for="(link,index) in playlistLink.m4a" :key="index" v-text="link"></div>
                    </div>
                </b-tab>
                <b-tab title="M3U8">
                    <div class="mt-3" v-if="playlistLink.m3u8.length > 0">
                        <button class="btn btn-info btn-block mb-2" v-clipboard:copy="playlistLink.m3u8.join('\r\n')">Copy To Clipboard</button>
                        <div v-for="(link,index) in playlistLink.m3u8" :key="index" v-text="link"></div>
                    </div>
                </b-tab>
