        </b-modal>
    </div>
</template>

<script>
import Loading from './Loading';
import { mapState, mapMutations } from 'vuex';
export default {
    name: "Modal",
    components: {
        Loading,
    },
    computed: {
        ...mapState('modal',[
            'downloadType',
            'downloadData',
            'collectionLink',
            'collectionPhoto',
            'albumLink',
            'playlistLink',
        ]),
    },
    methods: {
        ...mapMutations('modal',[
            'setCollectionLink',
            'setCollectionPhoto',
        ]),
        getMp3(link,quality) {
            if (link) {
                return link.replace('mp3/mp3-256', 'mp3/mp3-'+quality);
            }
        },
        getPodcast(link,quality) {
            if (link) {
                return link.replace('podcast/mp3-192', 'podcast/mp3-'+quality);
            }
        },
        hideModal (modalId) {
            this.$root.$emit('bv::hide::modal', modalId);
        },
        clearCollection () {
            this.setCollectionLink('');
            this.setCollectionPhoto('');
        },
    }
}
</script>