import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Playlist from "@/components/Playlist";
import Album from "@/components/Album";
import Podcast from "@/components/Podcast";
import Collection from "@/components/Collection";
Vue.use(Router);

export default new Router({
    // mode: 'history',
    base: process.env.NODE_ENV === 'development' ? '/' : '/rjdownloader/',
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/playlist/:id',
            name: 'Playlist',
            component: Playlist
        },
        {
            path: '/album/:id',
            name: 'Album',
            component: Album
        },
        {
            path: '/podcast',
            name: 'Podcast',
            component: Podcast
        },
        {
            path: '/collection',
            name: 'Collection',
            component: Collection
        }
    ]
})
