<script>
    import { mapState,mapMutations } from 'vuex';
    import axios from 'axios';
    export default {
        name: "Collection",
        computed: {
            ...mapState('modal',[
                'collectionLink',
                'collectionPhoto',
            ]),
        },
        data() {
            return {
                podcastLinkData : {},
                podcastLink : [],
                podcastPhotoData : {},
                podcastPhoto : [],
            }
        },
        methods: {
            ...mapMutations('modal',[
                'setCollectionLink',
                'setCollectionPhoto',
            ]),
            getPodcastArchive(name) {
                this.podcastLinkData = {};
                this.podcastLink = [];
                this.podcastPhotoData = {};
                this.podcastPhoto = [];
                this.setCollectionLink('');
                this.setCollectionPhoto('');
                let self = this;
                function getStuff(i){
                    axios.post(window.API_URL+'/search', {
                        query: name + ' ' + (new Date().getFullYear() - i)
                    }).then((response) => {
                        response.data.podcasts.forEach((index) => {
                            self.podcastLinkData[index.created_at] = index.link;
                            self.podcastPhotoData[index.created_at] = index.photo;
                        });
                        if (i === 12 ) {
                            Object.keys(self.podcastLinkData).sort().forEach(function(key) {
                                self.podcastLink.push(self.podcastLinkData[key]);
                            });
                            Object.keys(self.podcastPhotoData).sort().forEach(function(key) {
                                self.podcastPhoto.push(self.podcastPhotoData[key]);
                            });
                            self.setCollectionLink(self.podcastLink);
                            self.setCollectionPhoto(self.podcastPhoto);
                        }
                    }).catch((thrown) => {
                        console.log(thrown);
                    });
                }
                for (let i = 0; i < 13; i++) {
                    let k = i;
                    setTimeout(() => {
                        getStuff(i);
                    }, 200 * (k + 1));
                }
            }
        }
    }
</script>